import {
  ActiveElement,
  Chart,
  ChartConfiguration,
  ChartData,
  ChartEvent,
  ChartOptions,
  ScaleOptions,
} from "chart.js";
import { ZoomPluginOptions } from "chartjs-plugin-zoom/types/options";
import moment from "moment";

const maxTemp = [
  { x: moment("2021-08-18 03:00:00").valueOf(), y: -6.8 },
  { x: moment("2021-08-18 04:00:00").valueOf(), y: -6.2 },
  { x: moment("2021-08-18 05:00:00").valueOf(), y: -5.5 },

  { x: moment("2021-08-18 06:00:00").valueOf(), y: -2 },
  { x: moment("2021-08-18 07:00:00").valueOf(), y: 2 },
  { x: moment("2021-08-18 08:00:00").valueOf(), y: 0 },

  { x: moment("2021-08-18 09:00:00").valueOf(), y: -3 },
  { x: moment("2021-08-18 10:00:00").valueOf(), y: 2 },
  { x: moment("2021-08-18 11:00:00").valueOf(), y: -1 },
];

const minTemp = [
  { x: moment("2021-08-18 03:00:00").valueOf(), y: -7 },
  { x: moment("2021-08-18 04:00:00").valueOf(), y: -9 },
  { x: moment("2021-08-18 05:00:00").valueOf(), y: -11 },

  { x: moment("2021-08-18 06:00:00").valueOf(), y: -15 },
  { x: moment("2021-08-18 07:00:00").valueOf(), y: -7 },
  { x: moment("2021-08-18 08:00:00").valueOf(), y: -7.5 },

  { x: moment("2021-08-18 09:00:00").valueOf(), y: -6 },
  { x: moment("2021-08-18 10:00:00").valueOf(), y: -7 },
  { x: moment("2021-08-18 11:00:00").valueOf(), y: -6.5 },
];

const meanTemp = [
  { x: moment("2021-08-18 03:00:00").valueOf(), y: -7 },
  { x: moment("2021-08-18 04:00:00").valueOf(), y: -6.5 },
  { x: moment("2021-08-18 05:00:00").valueOf(), y: -6 },

  { x: moment("2021-08-18 06:00:00").valueOf(), y: -5.5 },
  { x: moment("2021-08-18 07:00:00").valueOf(), y: -5 },
  { x: moment("2021-08-18 08:00:00").valueOf(), y: -4.5 },

  { x: moment("2021-08-18 09:00:00").valueOf(), y: -4 },
  { x: moment("2021-08-18 10:00:00").valueOf(), y: -3.5 },
  { x: moment("2021-08-18 11:00:00").valueOf(), y: -3 },
];

const data: ChartData<"line"> = {
  datasets: [
    {
      label: "mean temp",
      data: meanTemp,
      borderColor: "#FFAB00",
      borderCapStyle: "round",
      borderDash: [6, 6],
    },
    {
      label: "min temp",
      data: minTemp,
      borderColor: "#525252",
      borderCapStyle: "round",
    },
    {
      label: "max temp",
      data: maxTemp,
      borderColor: "#1B079C",
      borderCapStyle: "round",
    },
  ],
};

const zoomOptions: ZoomPluginOptions = {
  zoom: {
    wheel: {
      enabled: true,
    },

    pinch: {
      enabled: true,
    },

    mode: "xy",

    onZoomComplete: ({ chart }: { chart: Chart }) => {
      console.log(`on zoom complete ${Math.random()}`);
    },
  },

  pan: {
    enabled: true,

    mode: "xy",

    onPanComplete: ({ chart }: { chart: Chart }) => {
      console.log(`on pan complete ${Math.random()}`);
    },
  },

  // limits: {
  //   y: {
  //     min: -100,
  //     max: 100,
  //   },
  //   x: {
  //     min: moment("2021-08-01 00:00:00").valueOf(),
  //     max: moment("2021-10-01 00:00:00").valueOf(),
  //   },
  // },
};

const xAxisOptions: ScaleOptions<"time"> = {
  grid: {
    color: "#E8E8E8",
    tickColor: "transparent",
  },

  type: "time",

  ticks: {
    autoSkip: false,
    autoSkipPadding: 10,
    maxRotation: 0,
  },
};

const yAxisOptions: ScaleOptions<"linear"> = {
  grid: {
    tickColor: "transparent",
    color: "#E8E8E8",
    drawBorder: true,
  },
};

const options: ChartOptions = {
  plugins: {
    zoom: zoomOptions,

    tooltip: {
      mode: "index",
      intersect: false,
    },
  },

  onHover(e: ChartEvent, elements: ActiveElement[], chart: Chart) {
    console.log(`on hover ${Math.random()}`);
  },

  scales: {
    x: xAxisOptions,
    y: yAxisOptions,
  },
};

export const chartConfig: ChartConfiguration = {
  type: "line",
  options: options,
  data,
};
